$(function(){
    $('.motto').textillate({ in: { effect: 'bounceIn' } });
    $('#content').fullpage({
        navigation:true,
        anchors:["page1","page2","page3","page4","page5"],
        // navigationPosition:right,
        afterLoad (anchorLink , index ) {
            if(index == 1){
                addClassPage1 ();
            }else if (index == 2){
                addClassPage2 ();
            }else if(index == 3){
                addClassPage3 ();
            }else if(index == 4){
                addClassPage4 ();
            }else if(index == 5){
                addClassPage5 ();
            }
        },
        onLeave (index ,nextIndex ,direction ) {
            if (index == 1) {
                removeClassPage1 ();
            }else if (index == 2){
                removeClassPage2 ();
            }else if(index == 3){
                removeClassPage3 ();
            }else if(index == 4){
                removeClassPage4 ();
            }else if(index == 5){
                removeClassPage5 ();
            }
        },
    })
    
});

